._2aFhM {
  width: 25px;
  height: 25px;
  border: 1.5px solid rgba(255,255,255,0.78039);
  border-radius: 50%;
  z-index: 4;
  pointer-events: none;
  top: 0px;
  left: 0px;
  transition: all 200ms ease-out;
  transform: translate(-50%, -50%);
  position: fixed;
  display: none;
}

._2jQ_z {
  background: rgba(255,255,255,0.78039);
  height: 4px;
  width: 4px;
  border-radius: 50%;
  z-index: 4;
  top: 0px;
  left: 0px;
  pointer-events: none;
  transform: translate(-50%, -50%);
  position: fixed;
  display: none;
}

/* to desktop */

@media (min-width: 769px) { 
  ._2aFhM, ._2jQ_z {
    display: block;
  }
}